import { ElDialog ,ElButton,ElInput} from "element-plus";
import { createVNode, defineComponent, reactive, render } from "vue";

const DialogComponent = defineComponent({
    props:{
        option:{type:Object}
    },
    setup(props,ctx){
        const state = reactive({
            option:props.option, // 
            isShow:false
        })
        ctx.expose({ // 
            showDialog(option){
                state.option = option;
                state.isShow = true;
            }
        });
        const onCancel = () =>{
            state.isShow = false;
        }
        const onConfirm = ()=>{
            state.isShow = false;
            state.option.onConfirm && state.option.onConfirm(state.option.content)
        }
        return ()=>{
            return <ElDialog v-model={state.isShow} title={state.option.title}>
                {{
                    default:()=><ElInput 
                        type="textarea" 
                        v-model={state.option.content}
                        rows={10}
                    ></ElInput>,
                    footer:()=> state.option.footer&& <div>
                        <ElButton onClick={onCancel}></ElButton>
                        <ElButton type="primary"  onClick={onConfirm}></ElButton>
                    </div>
                }}
            </ElDialog>
        }
    }   
})
let vm;
export function $dialog(option){
    // element-plusel-dialog 
    //    new SubComponent.$mount()
    if(!vm){
        let el = document.createElement('div');
        vm = createVNode(DialogComponent,{option}); // 
    
        // el 
        document.body.appendChild((render(vm,el),el)) // 
    }
    // el
    let {showDialog} = vm.component.exposed
    showDialog(option); // 
}