// 
// key

import {
  ElButton,
  ElDivider,
  ElInput,
  ElOption,
  ElSelect,
  ElRadio,
  CircleClose,
  ElRadioGroup,
  ElForm,
} from 'element-plus'
import Range from '../components/Range'
import img from '../assets/caption.png'
import dateRange from '../components/dateRange'
import { getParam, event_list } from '../packages/events.js'
import { handleBlur } from '../packages/form-validation'

function createEditorConfig() {
  const componentList = []
  const componentMap = {}

  return {
    componentList,
    componentMap,
    // 
    register: (component) => {
      // 
      componentList.push(component)
      // keykey
      componentMap[component.key] = component
    },
  }
}
export let registerConfig = createEditorConfig()

// 
// 
const createInputProp = (label) => ({ type: 'input', label })
const createColorProp = (label) => ({ type: 'color', label })
const createSelectProp = (label, options) => ({
  type: 'select',
  label,
  options,
})
const createFormProp = (label, options, id) => ({
  type: 'select',
  label,
  options,
  id
})
const createTableProp = (label, table) => ({ type: 'table', label, table })
const createImguploadProp = (label) => ({ type: 'imgupload', label })
const createSwitchProp = (label) => ({ type: 'switch', label })
const creatEventListProp = (label, List) => ({ type: 'button', label, List })
//  label 
// previewrenderkeyprops
//
const createRadioProp = (label, options) => ({ type: 'radio', label, options })
// 
registerConfig.register({
  label: '',
  preview: () => <img src={require('../assets/select.svg')} width={50}></img>,
  render: ({ props, model }) => {
    return (
      <ElSelect {...model.default}>
        {(props.options || []).map((opt, index) => {
          return (
            <ElOption
              label={opt.label}
              value={opt.value}
              key={index}></ElOption>
          )
        })}
      </ElSelect>
    )
  },
  key: 'select',
  props: {
    // [{label:'a',value:'1'},{label:'b',value:2}]
    options: createTableProp('', {
      options: [
        { label: '', field: 'label' },
        { label: '', field: 'value' },
      ],
      key: 'label', //  label
    }),
  },
  model: {
    // {default:'username'}
    default: '',
  },
})

// 
registerConfig.register({
  label: '',
  preview: () => <img src={require('../assets/text.svg')} width={50}></img>,
  render: ({ props }) => (
    <span style={{ color: props.color, fontSize: props.size }}>
      {props.text || ''}
    </span>
  ),
  key: 'text',
  props: {
    text: createInputProp(''),
    color: createColorProp(''),
    size: createSelectProp('', [
      { label: '14px', value: '14px' },
      { label: '20px', value: '20px' },
      { label: '24px', value: '24px' },
    ]),
  },
})

// 
registerConfig.register({
  label: '',
  resize: {
    width: true,
    height: true,
  },
  preview: () => <img src={require('../assets/button.svg')} width={50}></img>,
  render: ({ props, size }) => (
    <ElButton
      style={{ height: size.height + 'px', width: size.width + 'px', color: props.color, backgroundColor: props.backgroundColor }}
      type={props.type}
      size={props.size}
      disabled={props.switch1}
      onClick={() => getParam(props)}>
      {props.text || ''}
    </ElButton>
  ),
  key: 'button',
  props: {
    text: createInputProp(''),
    type: createSelectProp('', [
      { label: '', value: 'primary' },
      { label: '', value: 'success' },
      { label: '', value: 'warning' },
      { label: '', value: 'danger' },
      { label: '', value: 'text' },
    ]),
    size: createSelectProp('', [
      { label: '', value: '' },
      { label: '', value: 'medium' },
      { label: '', value: 'small' },
      { label: '', value: 'mini' },
    ]),
    switch1: createSwitchProp('Disabled'),
    color: createColorProp(''),
    backgroundColor: createColorProp(''),
    eventList: creatEventListProp('', {
      eventList: [
        { label: '', param: '', key: 'redirect', event: event_list[0] },
        { label: 'alert ', param: '', key: 'alert', event: event_list[1] },
      ],
    }),
  },
})

// 
registerConfig.register({
  label: '',
  resize: {
    width: true, // 
  },
  preview: () => <img src={require('../assets/input.svg')} width={50}></img>,
  render: ({ model, size, props }) => {
    return (
      <ElForm id="form" className="form" novalidate>
        <div className="form-control">
          <ElInput
            type={props.type}
            placeholder={props.placeholder}
            id={props.type}
            onBlur={(e) => { handleBlur(e) }}
            {...model.default}
            style={{ width: size.width + 'px' }}
            disabled={props.switch1}
            size={props.size}></ElInput>
          <small className="error-message"></small>
        </div>
      </ElForm>
    )
  },
  key: 'input',
  model: {
    // {default:'username'}
    default: '',
  },
  props: {
    placeholder: createInputProp('PlaceHolder'),
    switch1: createSwitchProp('Disabled'),
    size: createSelectProp('', [
      { label: '', value: '' },
      { label: '', value: 'large' },
      { label: '', value: 'small' },
    ]),
    type: createFormProp('', [
      { label: 'username', value: 'text' },
      { label: 'email', value: 'email' },
      { label: 'password', value: 'password' },
      { label: 'age', value: 'age' },
    ]),
  },
})

// 
registerConfig.register({
  label: '',
  preview: () => <img src={require('../assets/timeRange.svg')} width={50}></img>,
  render: ({ model, props }) => {
    return (
      <Range
        {...{
          timeStart: model.timeStart.modelValue, // @update:timeStart
          timeEnd: model.timeEnd.modelValue,
          'onUpdate:timeStart': model.timeStart['onUpdate:modelValue'],
          'onUpdate:timeEnd': model.timeEnd['onUpdate:modelValue'],
          size: props.size,
          title: props.title,
        }}></Range>
    )
  },
  model: {
    timeStart: '',
    timeEnd: '',
  },
  key: 'timeRange',
  props: {
    title: createInputProp(''),
    size: createSelectProp('', [
      { label: 'default', value: '' },
      { label: 'medium', value: 'medium' },
      { label: 'mini', value: 'mini' },
      { label: 'small', value: 'small' },
    ]),
  },
})

// 
registerConfig.register({
  label: '',
  preview: () => <img src={require('../assets/dateRange.svg')} width={50}></img>,
  render: ({ model, props }) => {
    return (
      <dateRange
        {...{
          dateStart: model.dateStart.modelValue, // @update:timeStart
          dateEnd: model.dateEnd.modelValue,
          'onUpdate:dateStart': model.dateStart['onUpdate:modelValue'],
          'onUpdate:dateEnd': model.dateEnd['onUpdate:modelValue'],
          size: props.size,
          title: props.title,
        }}></dateRange>
    )
  },
  model: {
    dateStart: '',
    dateEnd: '',
  },
  key: 'dateRange',
  props: {
    title: createInputProp(''),
    size: createSelectProp('', [
      { label: 'default', value: '' },
      { label: 'medium', value: 'medium' },
      { label: 'mini', value: 'mini' },
      { label: 'small', value: 'small' },
    ]),
  },
})

// 
registerConfig.register({
  label: '',
  resize: {
    width: true,
  },
  preview: () => <img src={require('../assets/divider.svg')} width={50}></img>,
  render: ({ props, size }) => (
    <div className="divider" style={{ width: size.width + 'px' }}>
      <ElDivider
        style={{
          backgroundColor: props.color,
        }}
        direction={props.type}
        content-position={props.position}>
        {props.text || ''}
      </ElDivider>
    </div>
  ),
  key: 'divider',
  props: {
    text: createInputProp(''),
    color: createColorProp(''),
    type: createSelectProp('', [
      { label: '', value: 'horizontal' },
      { label: '', value: 'vertical' },
    ]),
    position: createSelectProp('', [
      { label: '()', value: 'center' },
      { label: '', value: 'left' },
      { label: '', value: 'right' },
    ]),
  },
})

// 
registerConfig.register({
  label: '',
  resize: {
    width: true,
    height: true,
  },
  preview: () => <img src={require('../assets/img.svg')} width={50}></img>,

  render: ({ props, size }) => (
    <div
      className="uploadImg"
      style={{ width: size.width + 'px', height: size.height + 'px' }}
      onClick={() => getParam(props)}
    >
      <img src={props.url ? props.url : img}></img>
    </div>
  ),
  key: 'img',
  props: {
    url: createImguploadProp(''),
    eventList: creatEventListProp('', {
      eventList: [
        { label: '', param: '', key: 'redirect', event: event_list[0] },
        { label: 'alert ', param: '', key: 'alert', event: event_list[1] },
      ],
    }),
  },
})
//
registerConfig.register({
  label: '',

  preview: () => <img src={require('../assets/radio.svg')} width={50}></img>,
  render: ({ props }) => {
    return (
      <ElButton>
        <ElRadioGroup v-model={props.key}>
          {(props.options || []).map((opt, index) => {
            //label
            return <ElRadio label={opt.label}>{opt.label}</ElRadio>
          })}
        </ElRadioGroup>
      </ElButton>
    )
  },
  key: 'radio',
  props: {
    options: createTableProp('', {
      options: [{ label: '', field: 'label' }],
      key: 'label', //  label
    }),
  },

  model: {
    // {default:'username'}
    default: '',
  },
})
