// editor-rightdata

import { defineComponent, inject, watch, reactive } from 'vue'
import {
  ElForm,
  ElFormItem,
  ElButton,
  ElInputNumber,
  ElColorPicker,
  ElSelect,
  ElOption,
  ElInput,
  ElSwitch,
} from 'element-plus'
import deepcopy from 'deepcopy'
import TableEditor from './table-editor'
import EventList from './events-list'

export default defineComponent({
  props: {
    block: { type: Object }, // 
    data: { type: Object }, // 
    updateContainer: { type: Function },
    updateBlock: { type: Function },
  },
  setup(props, ctx) {
    const config = inject('config') // 
    const state = reactive({
      editData: {},
    })
    const reset = () => {
      if (!props.block) {
        // 
        state.editData = deepcopy(props.data.container)
      } else {
        state.editData = deepcopy(props.block)
      }
    }
    const apply = () => {
      if (!props.block) {
        // 
        props.updateContainer({ ...props.data, container: state.editData })
      } else {
        // 
        props.updateBlock(state.editData, props.block)
      }
    }
    watch(() => props.block, reset, { immediate: true })
    // content
    return () => {
      let content = []
      // 
      if (!props.block) {
        content.push(
          <>
            <ElFormItem label="">
              <ElInputNumber v-model={state.editData.width}></ElInputNumber>
            </ElFormItem>
            <ElFormItem label="">
              <ElInputNumber v-model={state.editData.height}></ElInputNumber>
            </ElFormItem>
          </>
        )
      } else {
        // key
        let component = config.componentMap[props.block.key]
        // 
        if (component && component.props) {
          // configtpropstype
          // {text:{type:'xxx'},size:{},color:{}}
          // Object.entries(component.props).map
          // propConfigtypelabeloptions
          content.push(
            Object.entries(component.props).map(([propName, propConfig]) => {
              return (
                <ElFormItem label={propConfig.label}>
                  {{
                    // type
                    input: () => (
                      <ElInput
                        v-model={state.editData.props[propName]}></ElInput>
                    ),
                    color: () => (
                      <ElColorPicker
                        v-model={
                          state.editData.props[propName]
                        }></ElColorPicker>
                    ),
                    select: () => (
                      <ElSelect v-model={state.editData.props[propName]}>
                        {propConfig.options.map((opt) => {
                          return (
                            <ElOption
                              label={opt.label}
                              value={opt.value}></ElOption>
                          )
                        })}
                      </ElSelect>
                    ),
                    table: () => (
                      <TableEditor
                        propConfig={propConfig}
                        v-model={state.editData.props[propName]}></TableEditor>
                    ),
                    imgupload: () => (
                      <input
                        style={{ width: 70 + 'px' }}
                        type="file"
                        accept="image/*"
                        onInput={(e) => {
                          // console.log('e.target.flies',e.target.files[0])
                          // input blurinputvalue
                          const reader = new FileReader()
                          reader.onload = () => {
                            state.editData.props[propName] = reader.result
                            // console.log('sss',state.editData.props[propName])
                          }
                          reader.readAsDataURL(e.target.files[0])
                        }}></input>
                    ),
                    switch: () => (
                      <ElSwitch
                        v-model={state.editData.props[propName]}></ElSwitch>
                    ),
                    radio: () => (
                      <ElRadio v-model={state.editData.props[propName]}>
                        {propConfig.options.map((opt) => {
                          return (
                            <ElOption
                              label={opt.label}
                              value={opt.value}></ElOption>
                          )
                        })}
                      </ElRadio>
                    ),
                    table: () => (
                      <TableEditor
                        propConfig={propConfig}
                        v-model={state.editData.props[propName]}></TableEditor>
                    ),
                    button: () => (
                      <EventList
                        propConfig={propConfig}
                        v-model={state.editData.props[propName]}></EventList>
                    ),
                  }[propConfig.type]()}
                </ElFormItem>
              )
            })
          )
        }

        if (component && component.model) {
          //                                                 default   
          content.push(
            Object.entries(component.model).map(([modelName, label]) => {
              return (
                <ElFormItem label={label}>
                  {/* model => {default:"username"} */}
                  <ElInput v-model={state.editData.model[modelName]}></ElInput>
                </ElFormItem>
              )
            })
          )
        }
      }

      return (
        <ElForm labelPosition="top" style="padding:0 30px">
          {content}
          <ElFormItem>
            <ElButton type="primary" onClick={() => apply()}>
              
            </ElButton>
            <ElButton onClick={reset}></ElButton>
          </ElFormItem>
        </ElForm>
      )
    }
  },
})
