import { computed, defineComponent, inject, ref } from 'vue'
import './editor.scss'
import EditorBlock from './editor-block'
import deepcopy from 'deepcopy'
import { useMenuDragger } from './useMenuDragger'
import { useFocus } from './useFocus'
import { useBlockDragger } from './useBlockDragger'
import { useCommand } from './useCommand'
import { $dialog } from '../components/Dialog'
import { $dropdown, DropdownItem } from '../components/Dropdown'
import EditorOperator from './editor-operator'
import { ElButton, ElScrollbar } from 'element-plus'
export default defineComponent({
  props: {
    modelValue: { type: Object },
    formData: { type: Object },
  },
  emits: ['update:modelValue'], // 
  setup(props, ctx) {
    //     
    const previewRef = ref(false)
    const editorRef = ref(true)

    const data = computed({
      get() {
        return props.modelValue
      },
      // 
      set(newValue) {
        ctx.emit('update:modelValue', deepcopy(newValue))
      },
    })
    const containerStyles = computed(() => ({
      width: data.value.container.width + 'px',
      height: data.value.container.height + 'px',
    }))

    const config = inject('config')

    const containerRef = ref(null)
    // 1.
    const { dragstart, dragend } = useMenuDragger(containerRef, data)

    // 2.
    let {
      blockMousedown,
      focusData,
      containerMousedown,
      lastSelectBlock,
      clearBlockFocus,
    } = useFocus(data, previewRef, (e) => {
      // 
      mousedown(e)
    })
    // 3.2
    let { mousedown, markLine } = useBlockDragger(
      focusData,
      lastSelectBlock,
      data
    )

    const { commands } = useCommand(data, focusData) // []
    const buttons = [
      { label: '', icon: 'icon-back', handler: () => commands.undo() },
      { label: '', icon: 'icon-forward', handler: () => commands.redo() },
      {
        label: '',
        icon: 'icon-export',
        handler: () => {
          $dialog({
            title: 'json',
            content: JSON.stringify(data.value),
          })
        },
      },
      {
        label: '',
        icon: 'icon-import',
        handler: () => {
          $dialog({
            title: 'json',
            content: '',
            footer: true,
            onConfirm(text) {
              //data.value = JSON.parse(text); // 
              commands.updateContainer(JSON.parse(text))
            },
          })
        },
      },
      {
        label: '',
        icon: 'icon-place-top',
        handler: () => commands.placeTop(),
      },
      {
        label: '',
        icon: 'icon-place-bottom',
        handler: () => commands.placeBottom(),
      },
      { label: '', icon: 'icon-delete', handler: () => commands.delete() },

      {
        label: () => (previewRef.value ? '' : ''),
        icon: () => (previewRef.value ? 'icon-edit' : 'icon-browse'),
        handler: () => {
          previewRef.value = !previewRef.value
          clearBlockFocus()
        },
      },
      {
        label: '',
        icon: 'icon-close',
        handler: () => {
          editorRef.value = false
          clearBlockFocus()
        },
      },
    ]

    const onContextMenuBlock = (e, block) => {
      e.preventDefault()

      $dropdown({
        el: e.target, // dropdown
        content: () => {
          return (
            <>
              <DropdownItem
                label=""
                icon="icon-delete"
                onClick={() => commands.delete()}></DropdownItem>
              <DropdownItem
                label=""
                icon="icon-place-top"
                onClick={() => commands.placeTop()}></DropdownItem>
              <DropdownItem
                label=""
                icon="icon-place-bottom"
                onClick={() => commands.placeBottom()}></DropdownItem>
              <DropdownItem
                label=""
                icon="icon-browse"
                onClick={() => {
                  $dialog({
                    title: '',
                    content: JSON.stringify(block),
                  })
                }}></DropdownItem>
              <DropdownItem
                label=""
                icon="icon-import"
                onClick={() => {
                  $dialog({
                    title: '',
                    content: '',
                    footer: true,
                    onConfirm(text) {
                      text = JSON.parse(text)
                      commands.updateBlock(text, block)
                    },
                  })
                }}></DropdownItem>
            </>
          )
        },
      })
    }

    return () =>
      !editorRef.value ? (
        <>
          <div
            class="editor-container-canvas__content"
            style={containerStyles.value}
            style="margin:0 auto">
            {data.value.blocks.map((block, index) => (
              <EditorBlock
                class="editor-block-preview"
                block={block}
                formData={props.formData}></EditorBlock>
            ))}
          </div>
          <div>
            <ElButton type="primary" onClick={() => (editorRef.value = true)}>
              
            </ElButton>
            {JSON.stringify(props.formData)}
          </div>
        </>
      ) : (
        <div class="editor">
          {/*  */}
          <div class="editor-left">
            <div class="editor-left-title"></div>
            {/*    h5*/}
            <div class="editor-left-material">
            {config.componentList.map((component) => (
              <div
                class="editor-left-material-item"
                //
                draggable
                //dragstart
                onDragstart={(e) => dragstart(e, component)}
                onDragend={dragend}>
                <span>{component.label}</span>
                <div>{component.preview()}</div>
              </div>
            ))}</div>
          </div>
          <div class="editor-top">
            {buttons.map((btn, index) => {
              const icon = typeof btn.icon == 'function' ? btn.icon() : btn.icon
              const label =
                typeof btn.label == 'function' ? btn.label() : btn.label
              return (
                <div class="editor-top-button" onClick={btn.handler}>
                  <i class={icon}></i>
                  <span>{label}</span>
                </div>
              )
            })}
          </div>
          <div class="editor-right">
            <ElScrollbar>
              <div class="editor-right-title">
                <p></p>
              </div>
              <EditorOperator
                block={lastSelectBlock.value}
                data={data.value}
                updateContainer={commands.updateContainer}
                updateBlock={commands.updateBlock}></EditorOperator>
            </ElScrollbar>
          </div>
          <div class="editor-container">
            {/*   */}
            <div class="editor-container-canvas">
              {/*  */}
              <div
                class="editor-container-canvas__content"
                style={containerStyles.value}
                ref={containerRef}
                // 
                onMousedown={containerMousedown}>
                {data.value.blocks.map((block, index) => (
                  <EditorBlock
                    class={block.focus ? 'editor-block-focus' : ''}
                    class={previewRef.value ? 'editor-block-preview' : ''}
                    block={block}
                    onMousedown={(e) => blockMousedown(e, block, index)}
                    onContextmenu={(e) => onContextMenuBlock(e, block)}
                    formData={props.formData}></EditorBlock>
                ))}

                {markLine.x !== null && (
                  <div class="line-x" style={{ left: markLine.x + 'px' }}></div>
                )}
                {markLine.y !== null && (
                  <div class="line-y" style={{ top: markLine.y + 'px' }}></div>
                )}
              </div>
            </div>
          </div>
          <div className="editor-watermark">
            <div className="editor-watermark-item"></div>
          </div>
        </div>
      )
  },
})
